.chat-elem-wrap {
  width: 100%;
  display: grid;
  grid-template-columns: 50px 140px;
  height: 70px;
  background: white;
  border-bottom: 2px solid #EBEEF2;
  padding: 10px;
  box-sizing: border-box;
  &.toggled {
    background: #f2f4f5; }
  img {
    width: 35px !important;
    height: 35px !important;
    border-radius: 500rem !important; }
  .avatar {
    display: flex;
    align-items: center; }
  .info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 90%; }
  .name-wrap {
    .name {
      font-size: 1em;
      color: rgba(0, 0, 0, .87);
      font-weight: 700; }
    .date {
      color: rgba(0,0,0,.4);
      font-size: .875em; }
    display: flex;
    justify-content: space-between; }
  &:hover {
    background: #f2f4f5;
    transition: .5s;
    cursor: pointer; }
  .first-line {
    display: flex;
    justify-content: space-between;
    .name-wrap {
      font-weight: 600; }
    .date-wrap {
      color: #6c757d;
      font-size: x-small; } }
  .second-line {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .message-wrap {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      font-size: smaller;
      font-weight: 600;
      color: #6c757d;
      width: 300px;
      text-align: left; } } }
